  gap: 17px;
  width: 15vw;
  padding-top: 20vh;
  padding-bottom: 5vh;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  margin-left: auto;
  margin-right: auto;
